'use client';

import { useEffect, useState } from 'react';
import CsvSummary from '@/components/CsvSummary';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

export default function DashboardPage() {
    const [csvData, setCsvData] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetchCsvData();
    }, []);

    const fetchCsvData = async () => {
        try {
            const response = await fetch('/api/csv-info', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ limit: 50 }),
            });
            const result = await response.json();
            if (result.success) {
                setCsvData(result.data);
            }
        } catch (error) {
            console.error('Failed to fetch CSV data:', error);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
            <div className="container mx-auto p-6">
                {/* Header */}
                <div className="mb-8">
                    <h1 className="text-4xl font-bold text-gray-900 dark:text-white mb-2">
                        Water Pollution Monitoring Dashboard
                    </h1>
                    <p className="text-gray-600 dark:text-gray-400">
                        CSV-driven real-time monitoring system
                    </p>
                </div>

                {/* CSV Summary */}
                <CsvSummary />

                {/* Recent Readings */}
                <Card className="mt-6">
                    <CardHeader>
                        <CardTitle>Recent Readings (from CSV)</CardTitle>
                    </CardHeader>
                    <CardContent>
                        {loading ? (
                            <div className="animate-pulse space-y-4">
                                <div className="h-4 bg-gray-200 rounded w-full"></div>
                                <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                                <div className="h-4 bg-gray-200 rounded w-1/2"></div>
                            </div>
                        ) : (
                            <div className="overflow-x-auto">
                                <table className="w-full text-sm">
                                    <thead>
                                        <tr className="border-b">
                                            <th className="text-left py-2 px-4 font-medium">Timestamp</th>
                                            <th className="text-left py-2 px-4 font-medium">Factory</th>
                                            <th className="text-left py-2 px-4 font-medium">Turbidity</th>
                                            <th className="text-left py-2 px-4 font-medium">pH</th>
                                            <th className="text-left py-2 px-4 font-medium">Chromium</th>
                                            <th className="text-left py-2 px-4 font-medium">Status</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {csvData.slice(0, 10).map((row: any, idx: number) => (
                                            <tr key={idx} className="border-b hover:bg-gray-50 dark:hover:bg-gray-800">
                                                <td className="py-3 px-4 text-xs">
                                                    {row.timestamp ? new Date(row.timestamp).toLocaleString() : 'N/A'}
                                                </td>
                                                <td className="py-3 px-4 font-medium">{row.factory_id || 'Baseline'}</td>
                                                <td className="py-3 px-4">{row.turbidity_ntu?.toFixed(2) || 'N/A'}</td>
                                                <td className="py-3 px-4">{row.ph?.toFixed(2) || 'N/A'}</td>
                                                <td className="py-3 px-4">{row.chromium_mg_l?.toFixed(3) || 'N/A'}</td>
                                                <td className="py-3 px-4">
                                                    <span
                                                        className={`px-2 py-1 rounded-full text-xs font-medium ${row.is_violation === 1
                                                                ? 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200'
                                                                : 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'
                                                            }`}
                                                    >
                                                        {row.is_violation === 1 ? 'Violation' : 'Compliant'}
                                                    </span>
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        )}
                    </CardContent>
                </Card>

                {/* Quick Stats */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-6">
                    <Card>
                        <CardHeader className="pb-3">
                            <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-400">
                                CSV Data Source
                            </CardTitle>
                        </CardHeader>
                        <CardContent>
                            <p className="text-2xl font-bold text-blue-600 dark:text-blue-400">public/data.csv</p>
                            <p className="text-xs text-gray-500 mt-1">Auto-detected structure</p>
                        </CardContent>
                    </Card>

                    <Card>
                        <CardHeader className="pb-3">
                            <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-400">
                                MongoDB Status
                            </CardTitle>
                        </CardHeader>
                        <CardContent>
                            <p className="text-2xl font-bold text-green-600 dark:text-green-400">Ready</p>
                            <p className="text-xs text-gray-500 mt-1">Users seeded, alerts enabled</p>
                        </CardContent>
                    </Card>

                    <Card>
                        <CardHeader className="pb-3">
                            <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-400">
                                API Endpoints
                            </CardTitle>
                        </CardHeader>
                        <CardContent>
                            <p className="text-2xl font-bold text-purple-600 dark:text-purple-400">3 Active</p>
                            <p className="text-xs text-gray-500 mt-1">csv-info, auth, alerts</p>
                        </CardContent>
                    </Card>
                </div>

                {/* API Testing */}
                <Card className="mt-6">
                    <CardHeader>
                        <CardTitle>API Endpoints (Test)</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-2 text-sm font-mono">
                            <div className="flex items-center gap-2">
                                <span className="px-2 py-1 bg-green-100 text-green-800 rounded text-xs font-bold">GET</span>
                                <code className="text-gray-700 dark:text-gray-300">/api/csv-info</code>
                                <span className="text-gray-500">- CSV metadata</span>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className="px-2 py-1 bg-blue-100 text-blue-800 rounded text-xs font-bold">POST</span>
                                <code className="text-gray-700 dark:text-gray-300">/api/auth/login</code>
                                <span className="text-gray-500">- Login (emp047/emp123)</span>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className="px-2 py-1 bg-green-100 text-green-800 rounded text-xs font-bold">GET</span>
                                <code className="text-gray-700 dark:text-gray-300">/api/alerts</code>
                                <span className="text-gray-500">- Fetch alerts</span>
                            </div>
                            <div className="flex items-center gap-2">
                                <span className="px-2 py-1 bg-blue-100 text-blue-800 rounded text-xs font-bold">POST</span>
                                <code className="text-gray-700 dark:text-gray-300">/api/alerts</code>
                                <span className="text-gray-500">- Create alert</span>
                            </div>
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
}
